html,
body,
#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
#root {
  overflow-x: auto;
}
.ellipsis-1 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.ellipsis-2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.ellipsis-3 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.link {
  color: #1890ff;
  cursor: pointer;
  -webkit-transition: color 500ms;
  transition: color 500ms;
}
.link:hover {
  color: #40a9ff;
}
.link[disabled] {
  color: #c0c0c0;
  cursor: default;
}
.ant-table-wrapper .ant-table {
  font-size: 13px;
}
.ant-table-wrapper .ant-table .ant-table-tbody tr:nth-of-type(even) {
  background-color: #f9fcfc;
}
.ant-table-wrapper .ant-table th {
  font-weight: bolder;
}
.ant-table-wrapper .ant-table .ant-btn {
  font-size: 12px;
  padding: 0 3px;
  height: 18px;
}
.ant-table-wrapper .ant-table-pagination.ant-pagination {
  margin: 6px 0 2px;
}
.ant-form .ant-form-explain,
.ant-form .ant-form-extra {
  font-size: 12px;
}
.table-filter-form .ant-form-item.hide {
  display: none;
}
.table-filter-form .ant-form-item-label {
  min-width: 100px;
}
.table-filter-form .ant-form-item-label label {
  font-size: 13px;
}
.table-filter-form .ant-form-item-control .ant-input,
.table-filter-form .ant-form-item-control .ant-select {
  font-size: 13px;
}
.table-filter-form .ant-btn {
  font-size: 12px;
}
.table-header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f2f3f4;
  border: 1px solid #e8e8e8;
  margin-bottom: 4px;
  padding: 4px 15px;
  font-size: 13px;
  color: #777;
}
.layout-flex {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.layout-flex .flex-header {
  display: flex;
  justify-content: space-between;
  padding: 4px 10px;
  border-bottom: 1px solid #e8e8e8;
  margin-bottom: 8px;
}
.layout-flex .flex-header .ant-btn {
  margin-right: 10px;
}
.layout-flex .flex-header .ant-btn:last-of-type {
  margin-right: 0;
}
.layout-flex .flex-filter {
  margin-bottom: 10px;
}
.layout-flex .flex-body {
  flex: 1 1;
  overflow: auto;
}
